import { Component, ViewChild, OnInit, OnDestroy, Input } from '@angular/core';
import { ANTLRInputStream, CommonTokenStream, ANTLRErrorListener, CommonToken } from 'antlr4ts';
import { ParseTreeWalker } from 'antlr4ts/tree';
import { BaneQLParser, QueryContext } from './src/antlr/BaneQLParser';
import { BaneQLLexer } from './src/antlr/BaneQLLexer';

import { AutoSuggester } from './src/suggest/autosuggest';
import {NgbTypeahead, NgbTypeaheadSelectItemEvent, NgbModal, NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {Observable, of, Subject, merge } from 'rxjs';
import {catchError, debounceTime, distinctUntilChanged, tap, switchMap, filter, delay, flatMap} from 'rxjs/operators';
import { MainService } from './src/main.service';

class ThrowErrorListener implements ANTLRErrorListener<any> {
  syntaxError: any;
}

@Component({
  selector: 'app-root',
  providers: [MainService],
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, OnDestroy {
  title = 'BaneQL Query Builder';
  model: any;
  searching = false;
  searchFailed = false;

  @ViewChild('instance') instance: NgbTypeahead;
  focus$ = new Subject<string>();
  click$ = new Subject<string>();

  startSerachTerm: string;
  endSearchTerm: string;


  tokenStream: CommonTokenStream;
  lexer: BaneQLLexer;
  parser: BaneQLParser;
  inputStream: ANTLRInputStream;

  query: MainService;
  walker: ParseTreeWalker;
  tree: QueryContext;


  errorToken: CommonToken;
  currentToken: CommonToken;
  isSyntaxError: boolean;
  isSuggestionsRequired = true;
  suggestions: any = [];
  customSuggestions = [];
  tokens = [];
  removeSuggestions = [];
  addSuggestions = [];

  // validating
  isSearchTermError = false;
  errorMessage: string;

  ngOnInit() {

  }


  ngOnDestroy() {

  }

  findCustomSuggestions(filteredSearchTerm: string) {
    const tokens = this.tokenStream.getTokens();
    if (this.currentToken.text === '<EOF>' && this.tokenStream.getTokens().length === 1) {
      this.customSuggestions.push('PropertyLikeValue', 'LiteralValue', 'Email', 'DomainOrIP', 'Number');
    } else if (this.currentToken.text === '(' || this.currentToken.text === ',') {
      this.customSuggestions.push('PropertyLikeValue', 'Number', 'String');
    } else if (this.currentToken.text === '=') {
      // suggest raw values
      this.customSuggestions.push('PropertyLikeValue', 'Number', 'String', 'LiteralValue');
    } else if (this.currentToken.text === '>=' || this.currentToken.text === '<=') {
      // suggest number values
      this.customSuggestions.push('Number');
    } else if (this.currentToken.text === '+' || this.currentToken.text === '-') {
      // show the matchers
      this.customSuggestions.push('PropertyLikeValue', 'Number', 'String', 'LiteralValue', 'DomainOrIP', 'Email');
    }
  }


  _filterSearchTerm(term: string) {
    let filteredSearchTerm: string;
    this.tokens = $.merge([], this.tokenStream.getTokens());
    if (this.tokens.length === 1 && this.tokens[0].text === '<EOF>') {
      this.currentToken = this.tokens[0];
      filteredSearchTerm = '';
    } else {
      // remove <EOF> token from tokens array.
      this.tokens.pop();
      this.currentToken = this.tokens.pop();
      if (this.currentToken.text === '>' || this.currentToken.text === '<') {
        filteredSearchTerm = this.currentToken.text;
      } else if (this.currentToken.text === '+' || this.currentToken.text === '-') {
        this.isSuggestionsRequired = false;
        this.suggestions.length = 0;
      } else if (this.currentToken.text === ')') {
        filteredSearchTerm = `>token(${this.currentToken.text}`;
      } else if (this.currentToken.text === '(') {
        this.isSuggestionsRequired = false;
        this.suggestions.length = 0;
        this.suggestions.push(')');
      } else {
        const isInMethodContext = this.isAutoSuggestInMethod();
        if (isInMethodContext) {
          // In methid context no need to invoke suggest engine
          this.isSuggestionsRequired = false;
          this.suggestions.length = 0;
          if (isInMethodContext && this.lexer.vocabulary.getSymbolicName(this.currentToken.type) === 'STRING'
                || isInMethodContext && this.lexer.vocabulary.getSymbolicName(this.currentToken.type) === 'PROPERTY') {
              if (this.tokens[0].text !== '=') {
                this.suggestions.push('=', ',', ')');
              } else {
                this.suggestions.push(',', ')');
              }
          } else if (isInMethodContext && this.lexer.vocabulary.getSymbolicName(this.currentToken.type) === 'NUMBER') {
              this.suggestions.push(',', ')');
          }
        } else {
          // check the type of string using lexer vocabulary
          if (this.currentToken.text === '=' || this.currentToken.text === '<=' || this.currentToken.text === '>=') {
            this.isSuggestionsRequired = false;
            this.suggestions.length = 0;
          } else if (this.lexer.vocabulary.getLiteralName(this.currentToken.type) === `'${this.currentToken.text}'`) {
            if (this.isSyntaxError) {
              filteredSearchTerm = `>${this.currentToken.text}`;
            } else {
              filteredSearchTerm = `${this.currentToken.text}`;
            }
          } else {
            if (this.tokens.length) {
              if (this.tokens[0].text === '=' || this.tokens[0].text === '<=' || this.tokens[0].text === '>=') {
                filteredSearchTerm = `token${this.tokens[0].text}${this.currentToken.text}`;
                if (this.isQueryEndwithSpace(term)) {
                  filteredSearchTerm = `${filteredSearchTerm} `;
                }
                this.removeSuggestions.push(',', ')');
              } else {
                filteredSearchTerm = `${this.currentToken.text}`;
                if (this.isQueryEndwithSpace(term)) {
                  filteredSearchTerm = `${filteredSearchTerm} `;
                }
              }
            } else {
              filteredSearchTerm = `${this.currentToken.text}`;
              if (this.isQueryEndwithSpace(term)) {
                filteredSearchTerm = `filteredSearchTerm `;
              }
            }
          }
        }
      }
    }
    return filteredSearchTerm;
  }

  isQueryEndwithSpace(term) {
    return term.charAt(term.length - 1) === ' ' ? true : false;
  }

  isAutoSuggestInMethod() {
    let isInMethodContext = false;
    $.each(this.tokens.reverse(), (index, value) => {
      if (value.text === ')') {
        isInMethodContext = false;
        return false;
      }
      if (value.text === '(') {
        isInMethodContext = true;
        return false;
      }
    });
    return isInMethodContext;
  }

  modifySuggestions() {
    if (this.removeSuggestions.length) {
      this.suggestions = this.suggestions.filter((value: any) => {
        return this.removeSuggestions.indexOf( value ) < 0;
      });
    }
  }

  validate() {
    this.isSearchTermError = false;
    const validateTerm: any = $('#typeahead-http').val();
    this._parserSearchTerm(validateTerm, true);

    setTimeout(() => {
      if (!this.isSearchTermError) {
        this.modalService.open(NgbdModalContent);
      }
    }, 1000);
  }

  clear() {
    $('#typeahead-http').val('');
    this.isSearchTermError = false;
  }

  private _parserSearchTerm(term: string, isValidate: boolean) {
    // read the input string and generate the tokens
    this.inputStream = new ANTLRInputStream(term);
    this.lexer = new BaneQLLexer(this.inputStream);
    this.lexer.removeErrorListeners();
    this.lexer.addErrorListener(new ThrowErrorListener());
    this.tokenStream = new CommonTokenStream(this.lexer);

    // read the tokens and get the parser
    this.parser = new BaneQLParser(this.tokenStream);
    this.parser.removeErrorListeners();
    const throwErrorListener = new ThrowErrorListener();
    throwErrorListener.syntaxError = (recognizer, offendingSymbol, line, column, msg, e) => {
      if (isValidate) {
        this.isSearchTermError = true;
        this.errorMessage = `${msg} at line ${line} column ${column}`;
      } else {
        try {
          this.errorToken = offendingSymbol;
          this.isSyntaxError = true;
        } catch (e) {
          this.customSuggestions.length = 0;
        }
      }
    };
    this.parser.addErrorListener(throwErrorListener);

    // create the tree and prepare the query
    this.tree = this.parser.query();

  }

  private _parseSearchTree() {
    // BaneQLListener implementor
    // this.query = new MainService();

    // Walk the tree - not required - for now
    // this.walker = new ParseTreeWalker();
    // this.walker.walk(this.query as any, this.tree);
  }

  _search(term: string): Observable<any> {

    this._parserSearchTerm(term, false);

    const suggested_delay = 0;
    const filteredSearchTerm = this._filterSearchTerm(term);

    // results gathering
    const results: { value: number, isCustom: boolean,  highlightClass: string}[] = [];

    if (this.isSuggestionsRequired) {
      const autosuggester =  new (AutoSuggester as any)(BaneQLLexer, BaneQLParser);
      this.suggestions = autosuggester.autosuggest(filteredSearchTerm);
    }

    // remove unwanted suggestions if any. Need fix - TODO
    this.modifySuggestions();

    this.findCustomSuggestions(filteredSearchTerm);

    // read the suggestions
    $.each(this.suggestions, function(index, value) {
      results.push({value: value, isCustom: false, highlightClass: ''});
    });

    // read the custom suggestions
    $.each(this.customSuggestions, function(index, value) {
      results.push({value: value, isCustom: true, highlightClass: 'disabledSuggestion'});
    });

    this.suggestions.length = 0;
    this.customSuggestions.length = 0;
    this.isSyntaxError = false;
    this.isSuggestionsRequired = true;
    this.removeSuggestions.length = 0;

    return of(null)
          .pipe(
          delay(suggested_delay),
          flatMap( () => {
            return of(results);
          }));
  }


  open(content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {

    }, (reason) => {

    });
  }

  constructor(private modalService: NgbModal) {}

  inputformatter = (result: any) => {
    return `${this.startSerachTerm}${result.value}${this.endSearchTerm}`;
  }

  selectItem = ($event: NgbTypeaheadSelectItemEvent) => {
    // TODO prevent item selection
    if ($event.item.isCustom) {
      $event.preventDefault();
    }
  }

  search = (text$: Observable<string>) => {
    const debouncedText$ = text$.pipe(debounceTime(200), distinctUntilChanged());
    const clicksWithClosedPopup$ = this.click$.pipe(filter(() => {
      return !this.instance.isPopupOpen();
    }));
    const inputFocus$ = this.focus$;

    return merge(debouncedText$, inputFocus$, clicksWithClosedPopup$).pipe(
      debounceTime(500),
      distinctUntilChanged(),
      tap(() => {
        return this.searching = true;
      }),
      switchMap(term => {

        const focusStart = $('#typeahead-http').prop('selectionStart');

        this.startSerachTerm = term.substr(0, focusStart);
        this.endSearchTerm = term.substr(focusStart, term.length);

        return this._search(this.startSerachTerm).pipe(
          tap(() => this.searchFailed = false),
          catchError(() => {
            this.searchFailed = true;
            return of([]);
          }));
        }
      ),
      tap(() => {
        this.searching = false;
      })
    );
  }
}


@Component({
  selector: 'ngbd-modal-content',
  template: `
    <div class="modal-body">
      <p>Success. It is Valid BaneQL Query</p>
    </div>
    <div class="modal-footer">
      <button type="button" class="btn btn-outline-dark" (click)="activeModal.close('Close click')">Close</button>
    </div>
  `
})
export class NgbdModalContent {
  @Input() name;

  constructor(public activeModal: NgbActiveModal) {}
}